
import { defineComponent, ref } from 'vue';
import { COM20700111_REQ_Body, COM20700111_RES_Body } from '@/shared/type/COM207001SC00';
import { PAGE_SIZE } from '@/shared/constants/common.const';
import pagination from '@/shared/components/Pagination/Pagination.vue';
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue';
import SelectOption from '@/shared/components/SelectOption/SelectOption.vue';

export default defineComponent({
    name: 'com207001sc00',
    components: {
        pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        return {
            headers: [
                { columnName: "No.", prop: "no" },
                { columnName: "View", prop: "view" },
                { columnName: "Role ID", prop: "userRoleID" },
                { columnName: "Role Name", prop: "userRoleName" },
                { columnName: "Role Description", prop: "userRoleDescription" },
                { columnName: "Status", prop: "userRoleStatusCode" }
            ],
            dataTable: [] as COM20700111_RES_Body[],
            portalRole: ref<COM20700111_REQ_Body>({
                userRoleID: "",
                userRoleName: "",
                userRoleStatusCode: "",
                pageNumber: 1,
                pageSize: PAGE_SIZE,
            }),
            startingIndex: 1 as number,
            totalCount: 0 as number,
            currentPage: 1 as number,
            isResetPage: false as boolean
        };
    },

    mounted() {
        this.requestPortalRoleList();
    },

    computed: {
        ScreenStatusCode() {
            return this.$store.state.CommonCodeModule.commonCode.ScreenStatusCode
        }
    },

    methods: {
        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.requestPortalRoleList();
        },

        handleStartIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        async requestPortalRoleList() {
            const body: COM20700111_REQ_Body = {
                userRoleID: this.portalRole.userRoleID,
                userRoleName: this.portalRole.userRoleName,
                userRoleStatusCode: this.portalRole.userRoleStatusCode,
                pageNumber: this.currentPage,
                pageSize: this.portalRole.pageSize,
            }
            const res: any = await this.$request.getRequest("COM20700111", body, false)
            this.dataTable = res?.body.list.map((data: COM20700111_RES_Body) => {
                return {
                    userRoleID: data.userRoleID,
                    userRoleDescription: data.userRoleDescription,
                    userRoleName: data.userRoleName,
                    userRoleStatusCode: `${data.userRoleStatusCode} : ${this.$codeConverter.convertValue("ScreenStatusCode", data.userRoleStatusCode)}`
                }
            });
            this.totalCount = res.body.totalCount;
        },

        handleClickAction(eventType: string, rowData?: COM20700111_RES_Body) {
            switch (eventType) {
                case 'register':
                    this.$router.push({
                        path: `COM207001SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'detail':
                    this.$router.push({
                        path: `COM207001SC01`,
                        query: {
                            type: "edit",
                            userRoleID: rowData?.userRoleID
                        }
                    });
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isResetPage = true
                        this.requestPortalRoleList();
                    } else {
                        this.requestPortalRoleList();
                    }
                    break;
                case 'clearSearch':
                    this.isResetPage = true;
                    this.portalRole.userRoleID = "";
                    this.portalRole.userRoleName = "";
                    this.portalRole.userRoleStatusCode = "";
                    this.portalRole.pageNumber = 1;
                    this.currentPage = 1;
                    this.requestPortalRoleList();
                    break;
            }

        },

    },
});
